import { createStyles, Header, Group, Burger, ActionIcon, Button, Transition, Paper } from '@mantine/core';
import { useDisclosure } from '@mantine/hooks';
import { IconBrandTwitter,IconBrandTelegram, IconBrandDiscord } from '@tabler/icons';
import { Link } from 'react-router-dom';

import { BNBIcon } from '../shared/icons/BNBIcon';
const HEADER_HEIGHT = 60;
const useStyles = createStyles((theme) => ({
  root: {
    position: 'relative',
    zIndex: 1,
  },

  dropdown: {
    position: 'absolute',
    top: HEADER_HEIGHT,
    left: 0,
    right: 0,
    zIndex: 0,
    borderTopRightRadius: 0,
    borderTopLeftRadius: 0,
    borderTopWidth: 0,
    overflow: 'hidden',

    [theme.fn.largerThan('sm')]: {
      display: 'none',
    },
  },
  header: {
    paddingLeft: theme.spacing.md,
    paddingRight: theme.spacing.md,
  },

  inner: {
    height: 56,
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },

  links: {
    [theme.fn.smallerThan('md')]: {
      display: 'none',
    },
  },

  burger: {
    [theme.fn.largerThan('sm')]: {
      display: 'none',
    },
  },

  link: {
    display: 'block',
    lineHeight: 1,
    padding: '8px 12px',
    borderRadius: theme.radius.sm,
    textDecoration: 'none',
    color: theme.colorScheme === 'dark' ? theme.colors.dark[0] : theme.colors.gray[7],
    fontSize: theme.fontSizes.sm,
    fontWeight: 500,

    '&:hover': {
      backgroundColor: theme.colorScheme === 'dark' ? theme.colors.dark[6] : theme.colors.gray[0],
    },
  },
}));



export function HomeHeaderNavBar(props) {
  const [opened, { toggle }] = useDisclosure(false);
  const { classes } = useStyles();

  const items = props.links.map((link) => (
    <a
      key={link.label}
      href={link.link}
      className={classes.link}
      onClick={(event) => event.preventDefault()}
    >
      {link.label}
    </a>
  ));

  return (
    <Header height={HEADER_HEIGHT} className={classes.header} mb={120}>
     
      <div className={classes.inner}>
        <Group>
          <Burger 
          className={classes.burger}
          opened={opened} onClick={toggle} size="sm" />
          <Transition transition="pop-top-right" duration={200} mounted={opened}>
          {(styles) => (
            <Paper className={classes.dropdown} withBorder style={styles}>
              {items}
            </Paper>
          )}
        </Transition>
          <BNBIcon size={28} />
        </Group>

        <Group>
          <Group ml={50} spacing={5} className={classes.links}>
            {items}
          </Group>
          <Group spacing="xs" position="right" noWrap>
          <ActionIcon size="lg" variant="default" radius="xl">
            <IconBrandTwitter size={18} stroke={1.5} />
          </ActionIcon>
          <ActionIcon size="lg" variant="default" radius="xl">
            <IconBrandTelegram size={18} stroke={1.5} />
          </ActionIcon>
          <ActionIcon size="lg" variant="default" radius="xl">
            <IconBrandDiscord size={18} stroke={1.5} />
          </ActionIcon>
        </Group>
        <Button
        variant="gradient"
        gradient={{ from: 'pink', to: 'yellow' }}
        size="xl"
        className={classes.control}
         color="yellow" radius="xl" sx={{ height: 30 }}
        component={Link} to="/dashboard"
        >
          Launch App
        </Button>
        </Group>
      </div>
    </Header>
  );
}